.plate-group--1,
.plate-group--2 {
  height: 100%;
  width: 25%;
  position: relative;
}

.plate-group--1 > *,
.plate-group--2 > * {
  background-color: #fff;
  width: 33.33%;
  border-radius: 15%;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

.plate-group__plate--small {
  height: 33.33%;
  left: 0;
}
.plate-group__plate--med {
  height: 66.67%;
  left: 33.33%;
}
.plate-group__plate--large {
  height: 100%;
  left: 66.67%;
}

/******************************************************************************/
.barbell-icon {
  position: relative;
  display: inline-block;
  border-radius: 15%;
  margin: 1px;
  height: 40px;
  width: 40px;
}

.barbell {
  height: 20px;
  width: 40px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%) rotate(-45deg);
}

.barbell__shaft {
  background-color: #fff;
  width: 100%;
  height: 15%;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.plate-group--2 {
  transform: scale(-1) translate(100%, 100%);
  left: 100%;
}

.plate-icon {
  position: relative;
  width: 40px;
  height: 40px;
}

.plate-icon > * {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  border-radius: 50%;
}

.plate__outline {
  height: 90%;
  width: 90%;
  color: transparent;
  border: solid 10px white;
}

.plate__circle {
  height: 10%;
  width: 10%;
  background-color: white;
}
