@-webkit-keyframes wiggle {
  0% {
    transform: translateX(0%);
  }
  33% {
    transform: translateX(-10%);
  }
  66% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(0%);
  }
}

@keyframes wiggle {
  0% {
    transform: translateX(0%);
  }
  33% {
    transform: translateX(-10%);
  }
  66% {
    transform: translateX(10%);
  }
  100% {
    transform: translateX(0%);
  }
}

